* {
  box-sizing: border-box;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  width: 100vw;
  height: 100vh;
}

.box {
  height: 100px;
  width: 100px;
  border: 1px solid black;
  display: inline-block;
  margin-right: 8px;
  border-radius: 5px;
  cursor: pointer;
}
