.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h2 {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  font-size: 30px;
  color: #333;
  margin: 70px 0 30px;
}

p {
  width: 500px;
  font-family: "Lato", sans-serif;
  font-weight: 300;
  font-size: 16px;
  color: #333;
  line-height: 26px;
  margin-top: 70px;
}
